* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

html {
  background-color: #fff;
  font-size: 62.5%;
}

body {
  color: #eee;
  background-color: #000;
  width: 41.2rem;
  height: 91.5rem;
  max-height: 100vh;
  margin: auto;
  font-family: Open Sans, sans-serif;
  font-size: 1.6rem;
}

.chat {
  height: 100%;
  overflow: auto;
}

.contacts {
  padding: 1rem;
}

.title {
  text-align: center;
  padding-top: 1rem;
  font-size: 2.4rem;
}

.messages {
  flex-direction: column;
  height: calc(100% - 13rem);
  padding: 0 1rem;
  display: flex;
  overflow: scroll;
}

.contactCard {
  align-items: center;
  gap: 1.5rem;
  padding: 1rem;
  display: flex;
}

.contactCard.selected {
  justify-content: space-between;
  margin: 0 1.5rem;
  padding: 1rem 0;
  position: sticky;
  top: 0;
}

.contactCard .name {
  font-size: 2rem;
  font-weight: 600;
}

.selected .name {
  font-weight: 500;
}

.chat .contactCard:not(:last-child) {
  border-bottom: .1rem solid #6f6f6fa1;
}

.dp {
  border-radius: 50%;
  width: 5rem;
}

.arrow {
  width: 2.5rem;
  margin-left: auto;
}

.selected .arrow {
  transform: rotate(180deg);
}

.contactCard.selected .arrow {
  margin-left: 0;
}

.send-form {
  z-index: 100;
  background-color: #000;
  padding: 1rem 1rem 2rem;
  position: fixed;
  bottom: 0;
}

.search-form {
  background-color: #000;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
}

.input-box {
  color: #fff;
  font-size: inherit;
  border: .3rem solid gray;
  border-radius: 100rem;
  outline: none;
}

.input-box:focus {
  font-size: inherit;
  border: .3rem solid #b3b3b3;
}

.input-box.search {
  background: #1a1a1a url("search.78c3fe3b.svg") 95% / 2.3rem no-repeat;
  width: 100%;
  margin-top: 1rem;
  padding: .5rem 2rem;
  font-size: 1.8rem;
}

.input-box.send {
  background: #1a1a1a url("send.a441811e.svg") 95% / 3rem no-repeat;
  width: 39.2rem;
  padding: 1rem 2rem;
}

.msg {
  border-radius: 1.8rem;
  width: auto;
  max-width: 60%;
  margin: 1rem;
  padding: .8rem 1.2rem;
}

.msg-right {
  border-bottom-right-radius: unset;
  background-color: #9acd32;
  margin-left: auto;
}

.msg-left {
  border-bottom-left-radius: unset;
  background-color: #f9629f;
  margin-right: auto;
}

.msg:first-child {
  margin-top: auto;
}

.last-msg {
  color: #ccc;
  font-size: 1.4rem;
}

::-webkit-scrollbar {
  width: .5rem;
}

::-webkit-scrollbar-track {
  display: none;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 2rem;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
